/* =========header====== */
#head {
  position: relative;
}

#header1 {
  background-image: url("../../public/images/ban2.jpg");
  background-size: cover;
  position: fixed;
  background-repeat: no-repeat;
  background-position: center;
  filter: grayscale(1);
  width: 100%;
  height: 700px;
}


#header1::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 700px;
  background-color: rgba(0, 0, 0, 0.5);
}

#header {
  justify-content: flex-start;
  align-items: flex-end;
  color: white;
  font-family: 'Poppins', sans-serif;
  width: 100%;
  height: 700px;
}


.bnd-d {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0% auto 3%;


}

.brand {
  width: 300px;
  height: 300px;
  animation: brand 1500ms;
  transition: transform .7s ease-in-out;
  z-index: 3;
  margin-top: 29%;
  position: absolute;
}

@keyframes brand {
  0% {
    transform: rotate(0deg);
  }

  /* 50%{
        transform: rotate(180deg);
    } */
  100% {
    transform: rotate(360deg);
  }
}

.brand:hover {
  transform: rotate(360deg);
}

.contain {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  margin-bottom: 5%;
}

#header .contain {
  width: 100%;
}

.header-content {
  margin-top: 20%;
  position: absolute;
  z-index: 2;
}

.header-content h1 {
  font-size: 4.5rem;
}

.header-content h5 {
  font-size: 200;
  letter-spacing: 4px;
  margin-top: -1.5rem;
}

.header-content .ico {
  width: 30px;
  height: 30px;
  color: #fff;
}

.py-7 {
  padding: 7rem 0;
}

/* button down and title -reusable */
.btn-down{
  font-size: 3rem;
  display: inline-block;
  margin-top: 1rem;
  transition: var(--transition);
}
.btn-down:hover{
  opacity: 0.8;
  transform: translateY(5px);
}
.btn-down-white{
  color: white;
}
.title{
  margin-bottom: 5rem;
}
.title h2{
  text-transform: uppercase;
  letter-spacing: 3px;
  font-weight: 600;
  position: relative;
}
.title h2::after{
  position: absolute;
  content: "";
  top: 120%;
  left: 0;
  width: 70px;
  height: 4px;
  background-color: black;
}


/* =======tag===== */



.tgsec{
  position: absolute;
  background-color:white;
  width: 100%;
  z-index: 2;
}

.container1 {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
}

.tg-cnt {
  width: 100%;
  height: 60vh;
  background-color: rgb(247, 246, 242);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.tag {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-size: 30px;
  line-height: 30px;
}

.snc {
  margin-inline: 7px;
  font-weight: 700;
}

.line {
  width: 70px;
  height: 4px;
  margin: 20px;
  background-color: #000;
}

/*======= about======= */


.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.about-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  gap: 2rem;
}

.about-img {
  width: 100%;
  height: auto;
}

.grid{
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 1rem;
}

.about-left {
  width: 50%;
  margin-bottom: 4rem;
  height: 480px;
  overflow: hidden;
}

.about-right {
  width: 50%;
}

.lead {
  opacity: 0.8;
  padding: 1rem 0;
  font-weight: 400;
  line-height: 1.8;
}





/* =====work====== */

.sec2{
  color: white;
  margin-block: 5%;
}

.workimg {
  background-image: url("../../public/images/work-pic.jpg");
  background-size: cover;
  position:-webkit-sticky;
  z-index: 1;
  background-repeat: no-repeat;
  width: 100%;
  height: 100vh;
}



.work{
  width: 100%;
  height: 100vh;
  position: absolute;
  z-index: 2;
  background-color: rgba(0, 0, 0, 0.4)
}

.work-content{
  width: 80%;
  margin-block: 5%;
  z-index: 4;
}

#work .title h2, #work h3{
  color: white;
}
#work .title h2::after{
  background: white;
}
.work-bottom{
  margin-top: 4rem;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  text-align: center;
}
.work-bottom > div .icon img{
  width: 50px;
  margin: 0 auto 1.7rem auto;
}

.workbt{
  width: 50px;
  height: 50px;
}

/* ======colors====== */

.col-d{
  width: 100%;
  margin-block: 10%;
  display: flex;
  justify-content: center;
}
.color{
  width: 80%;
  background: black;
  text-transform: uppercase;

}

.t1 .t2 .t3{
  width: 100%;
  /* background-clip: 'text'; */
  
  
}

.t1{
  font-size: 30px;
  padding-left: 10%;
  padding-top: 8%;
  margin-bottom: -5%;
  background: conic-gradient(from 90deg, white 50%, yellow,blue, green ,red,white
  ) fixed;
  background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
}

.t2{
  display: flex;
  justify-content: center;
  font-size: 200px;
  background: conic-gradient(from 90deg, white 50%, yellow,blue, green ,red,white
  ) fixed;
  /* background-size: 78%; */
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}
.t3{
  display: flex;
  justify-content: flex-end;
  font-size: 50px;
  padding-right: 10%;
  margin-top: -3%;
  margin-bottom: 3%;
  background: conic-gradient(from 90deg, white 50%, yellow,blue, green ,red,white
  ) fixed;
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
}

/* =====portfolio===== */

.portfolio{
  width: 100%;
}
.portfolio-grid > div{
  transition: var(--transition);
}
.portfolio-grid > div:hover{
  transform: scale(0.9);
}

.portfolio-grid{
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3,1fr);
}

.grid-col-span{
  grid-column: span 2;
}

/* images */
.img1{
  position: relative;

}

.im2{
  width: 50%;
}

.im1{
  width: 100%;
  height: 100%;
  object-fit: contain;
}
.im1-txt{
