  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.im2-txt{
  position: absolute;
  background:rgba(0,0,0,0.5);
  width: 50%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

}

.im2-d{
  width: 50%;
}

.exp{
  font-size: 20px;
  color: #fff;
}

a{
  text-decoration: none;
}

.im1-td{
