  font-size: 60px;
  font-weight: 500;
}

.im4-td{
  color: #fff;
  font-size: 50px;
  font-weight: 500;
}
.exp:hover{
  cursor: pointer;
  scale: 1.2;
  transition: var(--transition);
}

/* ======contact===== */
.sec4{
  width: 100%;
  
