}
.contact-content{
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: stretch;
  margin-block: 5%;
}
.contact-left{
  margin-bottom: 5rem;
}

.contact-right{
  border: 5px solid rgb(50, 49, 49);
