/* =======insta===== */

.sec5{
  width: 100%;
  height: 80vh;
  /* overflow: hidden; */
}

#insta{
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  z-index: 2;
  position: absolute;
  /* min-height: 65vh; */
  height: 80vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.insta-content{
  width: 100%;
}



.insta-grid{
  display: grid;
  grid-template-columns: repeat(4,1fr);
}

.inst-img{
  background-image: url("../../public/images/insta-pic.jpg");
  background-size: cover;
  /* position: fixed; */
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  z-index: 1;
}

#insta .title h2{
  color: var(--light-color);
}
#insta .title h2::after{
  background-color: var(--light-color);
}
.insta-grid > div{
  height: 400px;
  overflow: hidden;
  transition: var(--transition);
}
.insta-grid > div img{
  height: 60%;
  object-fit: cover;
}
.insta-grid > div:hover{
  transform: scale(0.9);
}

.in-img{
  width: 100%;
  /* height: 100px; */
}


