#footer{
  background-color: var(--dark-color);
  color: var(--light-color);
}
.footer-content{
  margin-block: 5%;
  text-align: center;
}
.footer-content h3{
  color: var(--light-color);
  font-size: 1.5rem;
  letter-spacing: 1px;
}
.footer-content ul{
  list-style: none;
  display: flex;
  flex-direction: row;
}
.footer-content ul li a{
  color: var(--light-color);
  font-size: 1rem;
  font: bold;
  transition: var(--transition);
  list-style: none;
  text-decoration: none;
}
.footer-content ul li a:hover{
  opacity: 0.7;
}
.footer-content ul li{
  margin: .5rem 1rem;
}
.footer-content > div:first-child{
  margin-bottom: 3rem;
}
.footer-content > div:first-child div{
  width: 100px;
  height: 100px;
  overflow: hidden;
  margin: 5rem auto;
  border-radius: 50%;
}

.foot-logo{
